<template name="bills">
	<view>
		<view v-for="(bill, index) in bills" :key="index" :style="[{animation: 'show ' + ((index+1)*0.2+1) + 's 1'}]">
			<view class="cu-bar bg-white solid-bottom margin-top-sm">
				<view class="action">
					<text class="cuIcon-title text-blue "></text> {{ bill.date }}
				</view>
				<view class="action text-sm text-grey">
					<view class="cu-capsule round">
						<view class='cu-tag bg-orange sm'>
							支
						</view>
						<view class="cu-tag line-orange sm">
							{{ bill.dayDec }}
						</view>
					</view>
					<view class="cu-capsule round">
						<view class='cu-tag bg-blue sm'>
							收
						</view>
						<view class="cu-tag line-blue sm">
							{{ bill.dayIn }}
						</view>
					</view>
				</view>
			</view>
			<view class="cu-list menu-avatar">
				<view class="cu-item" v-for="(list, index2) in bill.lists" :key="index2">
					<view class="cu-avatar"
						:style="{backgroundImage: 'url(' + list.categoryImg + ')'}">
					</view>
					<view class="content">
						<view class="text-grey">{{ list.categoryName }} <text class="text-grey text-xs cu-tag radius sm"
								style="margin-left: 20rpx;">{{ list.time }}</text></view>
						<view class="text-gray text-sm flex">
							<view class="text-cut">
								{{ list.note }}
							</view>
						</view>
					</view>
					<view class="action" style="width: auto; margin-right: 20rpx;">
						<view class="text-xl">{{ list.amount }}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "bills",
		props: {
			bills: {
				type: Array,
				default: [
					{
						date: "今天（09.06）",
						dayDec: "1234.11",
						dayIn: "12.34",
						lists: [
							{
								categoryImg: "../../static/images/a-tupianyihuifu-30.png",
								categoryName: "娱乐",
								time: "22:20",
								note: "娱乐支出",
								amount: "-145.00"
							},
							{
								categoryImg: "../../static/images/dache.png",
								categoryName: "交通出行",
								time: "22:20",
								note: "交通出行支出",
								amount: "-1450.00"
							}
						],
						
					},
					{
						date: "昨天（09.05）",
						dayDec: "12.30",
						dayIn: "120.34",
						lists: [
							{
								categoryImg: "../../static/images/fangzi.png",
								categoryName: "住房",
								time: "19:20",
								note: "",
								amount: "-2100.00"
							},
							{
								categoryImg: "../../static/images/food.png",
								categoryName: "餐饮消费",
								time: "08:20",
								note: "能吃是福",
								amount: "-258.00"
							}
						],
						
					}
				]
			}
		},
		data() {
			return {
				
			};
		},
		onShow() {
			console.log("success");
		},
		mounted() {
		}
	}
</script>

<style>	
	.cu-list.menu-avatar>.cu-item .content {
		width: calc(100% - 96rpx - 140rpx - 120rpx - 20rpx);
	}
	
	.cu-avatar {
		width: 80rpx;
		height: 80rpx;
		background-color: unset;
	}
</style>
